import { Component } from '@angular/core'

@Component({
    selector: 'ngl-input',
    template: `
        <p>
            <label>fake input</label>
            <input type="text" [(ngModel)]="value">
            {{value}}
        </p>`
})
export class NgLibWebpackInput {
    public value = 'empty';
}


